<template>
  <div>
    <div class="has-text-centered">
      <span class="is-size-3">
        <strong>
          {{ drug.name }}
        </strong>
      </span>
      <h6 class="subtitle is-6 is-italic">{{ drug.detail }}</h6>
    </div>
    <hr>
    <div>
      <span class="is-5">
        <strong>Dose: </strong> {{ drug.dose }} {{ drug.unit }}<br>
      </span>
      <span class="is-5">
        <strong>Prep: </strong> {{ drug.prep }} <br>
      </span>
    </div>
    <div class="columns is-mobile is-multiline is-gapless">
      <div v-show="isKg" class="column is-8">
        <input class="slider is-fullwidth is-success" min="3" max="100" v-model="weight" type="range">
      </div>
      <div v-show="isKg" class="column is-3 is-offset-1 mt-2">
        <span>{{ weight }} kg</span>
      </div>
      <div class="column is-8">
        <input class="slider is-fullwidth is-link" min="3" max="100" v-model="rate" type="range">
      </div>
      <div class="column is-3 is-offset-1 mt-2">
        <span>{{ rate }} ml/hr</span>
      </div>
      <div class="column is-2 pt-4">
        <strong>Conc: </strong>
      </div>
      <div class="column is-6">
        <div class="select is-success">
          <select v-model="conc">
            <option v-for="item in drug.conc" :key="item" :value="item">{{ item }}</option>
          </select>
        </div>
      </div>
      <hr>
      <span>
        <strong>Dose =</strong>
        {{ dose.toFixed(2) }} {{ drug.unit }}
      </span>

    </div>
    <div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'DrugInfo',
  props: {
    drug: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      weight: 60,
      rate: 10,
      conc: null,
    }
  },
  computed: {
    concNum () {
      if (!this.conc) return 1
      const [x, y] = this.conc.split(':').map(e => +e)
      return x/y
    },
    dose () {
      if (this.drug.unit === "mcg/kg/min") {
        return 100*this.concNum*this.rate/6/this.weight
      }
      if (this.drug.unit === "mcg/min") {
        return 100*this.concNum*this.rate/6
      }
      if (this.drug.unit === "mg/hr") {
        return this.rate*this.concNum
      }
      return 0
    },
    isKg () {
      return this.drug.unit.includes("kg")
    }
  },
  mounted () {
    this.conc = this.drug.conc[0]
  },
  watch: { 
    drug: function(newVal, oldVal) { // watch it
      oldVal
      this.conc = newVal.conc[0]
    }
  }
}

</script>
