<template>
  <div class="columns">
    <div class="column is-4">

      <div class="notification is-danger">
        <span class="title is-4">
          Drug Dose
        </span>
      </div>

      <div class="card"
        v-for="type in types"
        :key="type.name"
        >
        <header class="card-header">
          <p class="card-header-title">
          {{ type.name }}
          </p>
        <a :href="`#${type.name}-card`" data-action="collapse" class="card-header-icon is-hidden-fullscreen" aria-label="more options">
            <span class="icon">
              <i class="fas fa-angle-down" aria-hidden="true"></i>
            </span>
          </a>
        </header>
        <div :id="`${type.name}-card`" class="is-collapsible">
          <div class="card-content">
            <ul>
              <li v-for="item in filteredDrug(type.name)" :key="item.name">
                <a @click="selectedDrug = item.name">
                  <span class="icon">
                    <i class="fas fa-ellipsis-h"></i>
                  </span>
                  {{ item.name }}
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="column is-8">
      <DrugInfo v-if="drugInfo" :drug="drugInfo" />
    </div>

  </div>
</template>

<script>
import bulmaCollapsible from '@creativebulma/bulma-collapsible';
import DrugInfo from '@/components/DrugInfo'
import DrugData from '@/assets/drug-dose.json'
  // const axios = require('axios')
  export default {
    name: 'DrugDoseView',
    components: {
      DrugInfo
    },
    data: function(){
      return {
        searchKeyword: '',
        selectedDrug: null,
        loading: true,
        errored: false,
        drugs: null,
        types: null,
        active: null,
      }
    },
    computed: {
      drugInfo () {
        if (!this.drugs) return null
        return this.drugs.find(e => e.name == this.selectedDrug)
      }
    },
    methods: {
      filteredDrug (type) {
        return this.drugs.filter(e => e.type == type)
      },
      onClick (type) {
        // const i = this.types.findIndex(e => e.name == type)
        // this.types[i].active = true
        this.active = type
      }
    },
    async mounted () {
      // const urlDrug = 'http://localhost:3000/drugdose'
      // const urlType = 'http://localhost:3000/drugtype'
      // let res = await axios.get(urlDrug)
      // this.drugs = res.data
      // res = await axios.get(urlType)
      // this.types = res.data
      this.types = DrugData.drugtype
      this.drugs = DrugData.drugdose
      this.drugs = this.drugs.map(e => {
        e.conc = e.conc.split(',')
        e.alias = e.alias.split(',')
        return e
      })
      this.loading = false
      this.$nextTick(()=>{
        const bulmaCollapsibleInstances = bulmaCollapsible.attach('.is-collapsible');
        bulmaCollapsibleInstances.forEach(bulmaCollapsibleInstance => {
          // Check if current state is collapsed or not
          console.log(bulmaCollapsibleInstance.collapsed());
        });
      })
    }
  }
</script>

